@font-face {
  font-family: "PeopleFirstTtobaks";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/2406-2@1.0/PeoplefirstNeatLoudTTF.woff2")
    format("woff2");
  font-weight: normal;
  font-display: swap;
}
@font-face {
  font-family: "Cafe24Gowoonbam";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_twelve@1.1/Cafe24Oneprettynight.woff")
    format("woff");
  font-weight: normal;
  font-display: swap;
}
@font-face {
  font-family: "Cafe24Decobox";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/2405-3@1.1/Cafe24Decobox.woff2")
    format("woff2");
  font-weight: normal;
  font-display: swap;
}

a:link {
  text-decoration: none;
  color: #081377;
}
a:visited,
a:active {
  text-decoration: none;
  color: #081377;
}

.App {
  font-family: "Cafe24Gowoonbam";
  text-align: center;
  background-color: #f6f3a0;
  color: #081377;
  padding: 50px 0;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header-img-area {
  width: 180px;
  aspect-ratio: 5/5;
  border-radius: 50%;
  position: relative;
  margin-top: 80px;
}
.header-bg {
  width: 180px;
  aspect-ratio: 5/5;
  border-radius: 50%;
  background-color: white;
  position: absolute;
  top: 0;
}
.header-img {
  width: 100%;
  aspect-ratio: 5/5;
  border-radius: 50%;
  object-fit: cover;
  background-image: url(./one2.png);
  background-size: cover;
  background-position: -8px 0px;
  position: absolute;
  top: 0;
  z-index: 1;
}
.header-img-hat {
  width: 80px;
  position: absolute;
  top: -66px;
  left: 42px;
  z-index: 0;
  transform: rotate(-10deg);
}
.header-img-confetti {
  width: 20px;
  height: 20px;
  position: absolute;
}
.header-img-deco {
  width: 20px;
  height: 20px;
}
.App-main > p > span {
  color: #081377;
  font-weight: 800;
}
.call-icon {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 1px;
  margin-left: 4px;
}
h1,
h2,
h3 {
  color: #112ab6;
  margin: 30px 0;
}
@media screen and (max-width: 350px) {
  h1 {
    font-size: 28px;
  }
}
@media screen and (min-width: 350px) {
  h1 {
    font-size: 30px;
  }
}

h3 {
  font-family: "Cafe24Decobox";
  text-transform: uppercase;
  letter-spacing: 3px;
}
.move {
  padding-top: 20px;
  animation-duration: 5s;
  animation-name: show;
}
@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
